          <div style="margin: 50px 20px;">
            <t-divider align="left">开源项目</t-divider>
            <div style="margin-left: 90px;">
              <t-card bordered :style="{ width: '400px' }" title="Database-Export">
                <template #actions>
                  <t-link theme="primary" hover="color" href="https://github.com/PomZWJ/database-export" target="_blank">
                    <jump-icon slot="suffixIcon" />
                  </t-link>
                </template>
                一款多线程生成数据库结构文档的开源springboot工程，能支持最新的数据库版本，支持导出多种格式的文件，也能直接在网页上预览,
                支持
                MySQL
                ORACLE
                SqlServer
                Postgresql
                Clickhouse
                Sqlite3
                DB2
                达梦
                <template #footer>
                  <t-row :align="'middle'" style="gap: 24px">
                    <t-col flex="auto" style="display: inline-flex; justify-content: left">
                      <a style="margin-right: 10px;" href="https://gitee.com/pomz/database-export/stargazers"><img src="https://gitee.com/pomz/database-export/badge/star.svg"></a>
                      <!--
