                      -->
                      <a style="margin-right: 10px;" href="https://github.com/PomZWJ/database-export"><img src="https://img.shields.io/github/stars/PomZWJ/database-export?logo=github"></a>
                      <!--
                      <a style="margin-right: 10px;" href="https://github.com/PomZWJ/database-export/network"><img src="https://img.shields.io/github/forks/PomZWJ/database-export?logo=github"></a>
                      -->
                    </t-col>
                  </t-row>
                </template>
              </t-card>
            </div>
          </div>
          <div style="margin: 50px 20px;">
            <t-divider align="left">工作经验</t-divider>
            <t-timeline mode="same" style="margin-left: 90px;">
              <t-timeline-item>
                <div>
                  <t-tag theme="primary" size="large">2022-至今</t-tag>
                  <template style="display: flex;flex-direction: row;align-items: center">
                    <span style="margin-left: 10px">厦门纵行科技有限公司</span>
                    <t-tag style="margin-left: 10px;" variant="outline" theme="success">物联网</t-tag>
                  </template>
                </div>
                <div class="timeline-custom-content">
                  <p>1.利用Flink,使物联网设备上传上来的数据进行数据的分析,包括EventTime、CP、SP，涉及多种窗口</p>
                  <p>2.对系统进行优化,包括Http API可视化配置、MQTT接口,导入导出,国际化等</p>
                  <p>3.涉及与物联设备进行数据交互，包含上下行数据</p>
                  <p>4.前端VUE/小程序开发</p>
                </div>
              </t-timeline-item>
              <t-timeline-item>
                <div>
                  <t-tag theme="primary" size="large">2020-2022</t-tag>
                  <template style="display: flex;flex-direction: row;align-items: center">
                    <span style="margin-left: 10px">厦门服云(安全狗)科技有限公司</span>
                    <t-tag style="margin-left: 10px;" variant="outline" theme="success">云安全</t-tag>
                  </template>
                </div>
                <div class="timeline-custom-content">
                  <p>1.使用SpringCloud开发基于Agent对服务器的扫描数据处理，包含漏洞处理，Docker镜像的安全扫描</p>
                  <p>2.使用cancal处理政务网与互联网之间的数据同步问题</p>
                  <p>3.出差客户现场，与客户有良好的沟通经验</p>
                </div>
              </t-timeline-item>
              <t-timeline-item>
                <div>
                  <t-tag theme="primary" size="large">2018-2020</t-tag>
                  <template style="display: flex;flex-direction: row;align-items: center">
                    <span style="margin-left: 10px">厦门易联众易惠科技有限公司</span>
                    <t-tag style="margin-left: 10px;" variant="outline" theme="success">医疗行业</t-tag>
                  </template>
                </div>
                <div class="timeline-custom-content">
                  <p>1.打造医院统一支付平台,支持聚合支付,微信、支付宝、银联的接口,使用Dubbo+SSM框架</p>
                  <p>2.实现三方的对账，涉及到和微信(支付宝)、POS等对账等功能，以及用IReport生成财务报表，提供财务核对</p>
                  <p>3.对接医院HIS等医院第三方系统的接口,接口类型包括http、webservice、sql存储过程等</p>
                </div>
              </t-timeline-item>
            </t-timeline>
          </div>
